.splashscreen {
    margin-left: 0em;
    -webkit-transform: scale(2, 2);
        -ms-transform: scale(2, 2);
            transform: scale(2, 2);
}

.t1 {
    position: absolute;
}

.i {
    position: absolute;
    margin-top: 2em;
    margin-left: -1em;
    -webkit-animation-name: spini;
            animation-name: spini;
    -webkit-animation-duration: 4000ms;
            animation-duration: 4000ms;
    -webkit-animation-delay: 3000ms;
            animation-delay: 3000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.i .p1 {
    background-color: #438187;
    width: 1em;
    height: 4em;
}

.z {
    position: absolute;
    margin-top: 5.5em;
    margin-left: 5em;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
    -webkit-animation-name: spinz;
            animation-name: spinz;
    -webkit-animation-duration: 4000ms;
            animation-duration: 4000ms;
    -webkit-animation-delay: 2000ms;
            animation-delay: 2000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.z .p1 {
    background-color: #AA4942;
    width: 1em;
    height: 2em;
    top: .5em;
    right: .5em;
    position: absolute;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.z .p2 {
    background-color: #AA4942;
    width: 1em;
    height: 2em;
    top: -.5em;
    right: -.5em;
    position: absolute;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.l {
    position: absolute;
    margin-top: 5em;
    margin-left: -1em;
    -webkit-animation-name: spinl;
            animation-name: spinl;
    -webkit-animation-duration: 4000ms;
            animation-duration: 4000ms;
    -webkit-animation-delay: 1000ms;
            animation-delay: 1000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.l .p1 {
    background-color: #F5C443;
    width: 1em;
    height: 3em;
    top: .5em;
    right: .5em;
    position: absolute;
}

.l .p2 {
    background-color: #F5C443;
    width: 1em;
    height: 2em;
    top: 2.5em;
    right: 0em;
    position: absolute;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.o {
    position: absolute;
    margin-top: 9em;
    margin-left: -.5em;
    -webkit-animation-name: spino;
            animation-name: spino;
    -webkit-animation-duration: 4000ms;
            animation-duration: 4000ms;
    -webkit-animation-delay: 1000ms;
            animation-delay: 1000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.o .p1 {
    background-color: #3F4C4F;
    width: 2em;
    height: 2em;
}

/* animation */

@-webkit-keyframes spinz {
    0% {
        -webkit-transform: scale(1, 1) rotate(90deg);
                transform: scale(1, 1) rotate(90deg);
    }
    10% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    40% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    70% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
    100% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
}

@keyframes spinz {
    0% {
        -webkit-transform: scale(1, 1) rotate(90deg);
                transform: scale(1, 1) rotate(90deg);
    }
    10% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    40% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    70% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
    100% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
}

@-webkit-keyframes spinl {
    0% {
        -webkit-transform: scale(1, 1) rotate(0deg);
                transform: scale(1, 1) rotate(0deg);
    }
    40% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    50% {
        -webkit-transform: scale(1.2, 1.2);
                transform: scale(1.2, 1.2);
    }
    60% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes spinl {
    0% {
        -webkit-transform: scale(1, 1) rotate(0deg);
                transform: scale(1, 1) rotate(0deg);
    }
    40% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    50% {
        -webkit-transform: scale(1.2, 1.2);
                transform: scale(1.2, 1.2);
    }
    60% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@-webkit-keyframes spini {
    0% {
        -webkit-transform: scale(1, 1) rotate(0deg);
                transform: scale(1, 1) rotate(0deg);
    }
    70% {
        -webkit-transform: skew(0deg,0deg);
                transform: skew(0deg,0deg);
    }
    80% {
        -webkit-transform: skew(15deg,15deg);
                transform: skew(15deg,15deg);
    }
    90% {
        -webkit-transform: skew(-15deg,-15deg);
                transform: skew(-15deg,-15deg);
    }
    100% {
        -webkit-transform: skew(0deg, 0deg);
                transform: skew(0deg, 0deg)
    }
}

@keyframes spini {
    0% {
        -webkit-transform: scale(1, 1) rotate(0deg);
                transform: scale(1, 1) rotate(0deg);
    }
    70% {
        -webkit-transform: skew(0deg,0deg);
                transform: skew(0deg,0deg);
    }
    80% {
        -webkit-transform: skew(15deg,15deg);
                transform: skew(15deg,15deg);
    }
    90% {
        -webkit-transform: skew(-15deg,-15deg);
                transform: skew(-15deg,-15deg);
    }
    100% {
        -webkit-transform: skew(0deg, 0deg);
                transform: skew(0deg, 0deg)
    }
}

@-webkit-keyframes spino {
    0% {
        -webkit-transform: scale(1, 1) rotate(90deg);
                transform: scale(1, 1) rotate(90deg);
    }
    10% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    40% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    70% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
    100% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
}

@keyframes spino {
    0% {
        -webkit-transform: scale(1, 1) rotate(90deg);
                transform: scale(1, 1) rotate(90deg);
    }
    10% {
        -webkit-transform: scale(.8, .8);
                transform: scale(.8, .8);
    }
    40% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    70% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
    100% {
        -webkit-transform: rotate(450deg);
                transform: rotate(450deg);
    }
}


